
















export default{
	data(){
		taskType:'';
	},
	methods:{
		ysTask(){
			this.taskType ='01';
			uni.setStorageSync('taskType',this.taskType);
			uni.navigateTo({
				url:'/pages/TaskCenter/taskHall/ysTask'
			})
		},
		freeTask(){
			this.taskType ='02';
			uni.setStorageSync('taskType',this.taskType);
			uni.navigateTo({
				url:'/pages/TaskCenter/taskHall/ysTask'
			})
		}
	}
}



